li {
  display: flex;
  margin: 10px;
  align-items: center;
  width: 70%;
  height: 200px;
  min-width: 350px;
  border-bottom: gold 1px solid;
  a {
    height: 100%;
    width: 100%;
  }

  .cats-img {
    width: 30%;
    min-width: 100px;
    height: 100%;
    border-radius: 60px;
    margin-right: 30px;
  }
  h2 {
    color: black;
  }
}

// ul {
//   width: 100%;
// }

.contactPrev {
  display: flex;
  flex-direction: row;
  height: 100%;
  width: 100%;
}

.contact-details {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
@media (max-width: 500px) {
  img.cats-img {
    margin-right: 0px !important;
  }
  .robot-list {
    padding-left: 0;
  }
}
