.contact-full-details {
  display: flex;
  flex-direction: row-reverse;
  justify-content: center;
  align-items: center;
  align-content: flex-start;
}

.details-btn {
  button {
    border-radius: 10px;
    outline: none;
    height: 30px;
    width: 60px;
    margin-right: 10px;
    border: none;
    background-color: #be4145;
  }
}

.Transfer-btn {
  border-radius: 10px;
  outline: none;
  width: 80px;
  height: 30px;
  cursor: pointer;
  border: none;
  background-color: #be4145;
}

@media (max-width: 550px) {
  .contact-full-details {
    flex-direction: column-reverse;
  }
}
